import { NgModule } from "@angular/core";
import { CommonModule } from "@angular/common";
import { ExampleHost } from "./example.host.directive";
import { ExampleComponent } from "./example.component";
import { ExamplePartComponent } from "./example.part.component";

@NgModule({
  imports: [CommonModule],
  declarations: [ExampleHost, ExampleComponent, ExamplePartComponent],
  exports: [ExampleHost, ExampleComponent, ExamplePartComponent],
  entryComponents: [ExamplePartComponent]
})
export class ExampleModule {}



