import { Component } from "@angular/core";
@Component({
  selector: "example",
  template: `
    <div style="padding: 50px">
      <a type="button"
              tooltip="Vivamus sagittis lacus vel augue laoreet rutrum faucibus."
              placement="bottom">
        tooltip.
      </a>
    </div>`
})
export class Example {}



