import { Component } from "@angular/core";
@Component({
  selector: "example",
  template: `
    <BHPAlert color="primary">
    <i class="fas fa-star md-18"></i>
    <strong>Primary!</strong> This is a primary message. We really want your
  </BHPAlert>
  <BHPAlert color="success">
    <i class="fas fa-check md-18"></i>
    <strong>Success!</strong> A copy of your selected purchase orders has been sent to your customers.
  </BHPAlert>
  <BHPAlert color="info">
    <i class="fas fa-info-circle md-18"></i>
    <strong>Info!</strong> This is an info message. We have stuff for you.
    <a class="alert-link" href="https://www.bhp.com">This is a link.</a>
  </BHPAlert>
  <BHPAlert color="warning">
    <i class="fas fa-exclamation-triangle md-18"></i>
    <strong>Warning!</strong> This is a warning message. We need your attention.
  </BHPAlert>
  <BHPAlert color="danger">
    <i class="fas fa-exclamation-circle md-18"></i>
    <strong>Danger!</strong> This is a danger message.
    <a class="alert-link" href="https://www.bhp.com">This is a link.</a>
  </BHPAlert>`
  })
export class Example {}



