import {
  Component,
  Input,
  ViewChild,
  ComponentFactoryResolver,
  Type
} from "@angular/core";
import { ExampleHost } from "./example.host.directive";

@Component({
  selector: "ExamplePart",
  template: `
            <section>
              <h4 class="h4-light" *ngIf="title && title.length > 0" >
                  {{title}}
              </h4>

              <p *ngIf="introduction && introduction.length>0">
                {{introduction}}
              </p>

              <div class="example-display">
                <div *ngIf="subTitle && subTitle.length>0"
                  class="example-sub-title">
                    {{subTitle}}
                </div>
                <div class="example-html" >
                  <ng-template example-host></ng-template>
                </div>
              </div>
              <hr />
            </section>`,
  styleUrls: ["./example.component.scss"]
})
export class ExamplePartComponent {
  @ViewChild(ExampleHost) adHost: ExampleHost;

  @Input()
  set example(value: any) {
    if (!value) {
      console.error("Set example does not accecpt null");
    } else {
      if (Array.isArray(value)) {
        this._example = value;
      } else {
        this._example = [value];
      }
      this.loadComponent();
    }
  }
  get example() {
    return this._example;
  }

  _example: any;
  subTitle: string;
  title: string;
  introduction: string;

  constructor(private componentFactoryResolver: ComponentFactoryResolver) {}

  loadComponent() {
    let viewContainerRef = this.adHost.viewContainerRef;
    viewContainerRef.clear();

    this._example.forEach(example => {
      viewContainerRef.createComponent(
        this.componentFactoryResolver.resolveComponentFactory(example)
      );
    });
  }
}



