/**
 * use "ngx-breadcrumbs" please refer to https://github.com/McNull/ngx-breadcrumbs
 *
 */

import { NgModule, APP_INITIALIZER, Injector } from "@angular/core";
import { CommonModule } from "@angular/common";
import { RouterModule, Routes, Router } from "@angular/router";
import { FormsModule } from "@angular/forms";
import * as camelcase from "camelcase";

import { ExampleComponent } from "../Example/example.component";
import { ExampleModule } from "../Example/example.module";
import { RouterConfig } from "./router.config";

import { AngularImports, Examples } from "../../.cache/template";

import {
  PaginationModule,
  ProgressbarModule,
  TooltipModule,
  PopoverModule
} from "ngx-bootstrap";
import { McBreadcrumbsModule } from "ngx-breadcrumbs";

import {
  BHPAccordionModule,
  BHPAlertModule,
  BHPBadgeModule,
  ButtonsModule,
  BHPCardModule,
  BHPCheckboxModule,
  BHPIconModule,
  BHPLogoModule,
  BHPListModule,
  BHPModalModule,
  BHPNavbarModule,
  BHPAppbarModule,
  BHPDropdownModule,
  BHPSwitchModule,
  BHPSpinnerModule
} from "bhp-ui-angular";

const createRoutes = RouterConfig =>
  RouterConfig.map(entry => renderEntry(entry));

const renderEntry = ({ name, linkTo, subMenu }) => {
  let example = Examples[camelcase(linkTo)];
  let route = {
    path: linkTo,
    component: ExampleComponent,
    data: {
      breadcrumbs: name, // Interpolates values resolved by the router
      components: example ? example.components : [],
      yaml: example ? example.yaml : {}
    },
    children: []
  };
  if (subMenu) {
    route.children = createRoutes(subMenu);
  }
  return route;
};

@NgModule({
  declarations: AngularImports,
  entryComponents: [ExampleComponent, ...AngularImports],
  imports: [
    FormsModule,
    ExampleModule,
    CommonModule,
    RouterModule.forRoot([]),
    BHPAlertModule.forRoot(),
    BHPAccordionModule.forRoot(),
    BHPBadgeModule.forRoot(),
    ButtonsModule.forRoot(),
    BHPCardModule.forRoot(),
    BHPCheckboxModule.forRoot(),
    BHPIconModule.forRoot(),
    BHPLogoModule.forRoot(),
    BHPListModule.forRoot(),
    BHPModalModule.forRoot(),
    BHPNavbarModule.forRoot(),
    BHPAppbarModule.forRoot(),
    BHPDropdownModule.forRoot(),
    BHPSwitchModule.forRoot(),
    BHPSpinnerModule.forRoot(),
    PaginationModule.forRoot(),
    TooltipModule.forRoot(),
    ProgressbarModule.forRoot(),
    McBreadcrumbsModule.forRoot(),
    PopoverModule.forRoot()
  ],

  exports: [RouterModule],
  providers: [
    // need this provider to set routes, otherwise, it reports: cannot loadchildren of undefined.
    // Or we should hard coded all routes.
    {
      provide: APP_INITIALIZER,
      useFactory: configServiceFactory,
      deps: [Injector],
      multi: true
    }
  ]
})
export class AppRoutingModule {}

export function configServiceFactory(injector: Injector): Function {
  return () => {
    var router: Router = injector.get(Router);
    return router.resetConfig(
      createRoutes(RouterConfig.filter(route => route.name === "UI components"))
    );
  };
}



