import { Component } from "@angular/core";
@Component({
  selector: "example",
  template: `
    <BHPSwitch [isOn]="false" switchClass="">
      <div left>YES</div>
      <div right>NO</div>
    </BHPSwitch>
    <BHPSwitch [isOn]="true" switchClass="">
      <div left>YES</div>
      <div right>NO</div>
    </BHPSwitch>`
})
export class Example {}



