import { Component } from "@angular/core";
@Component({
  selector: "example",
  styleUrls: ["./sidebar0.scss"],
  template: `
    <div class="container-main" style="height: 400px">
      <div class="container-sidebar" [ngClass]="{'sidebar-show': isOpen}">
        <div class="sidebar-backdrop" (click)="isOpen=!isOpen"></div>
        <div class="sidebar">
          <div class="entry" style="background-color: #393b42">
            <div class="link" (click)="this.selectItem = 1">
              <i class="fas fa-file link-icon"></i>
              UI components 1
              <i class="fas toggle" [ngClass]="{'fa-angle-down' : this.selectItem === 1, 'fa-angle-right':this.selectItem !== 1}"></i>

            </div>
            <div *ngIf="this.selectItem === 1">
              <a class="link" href="">example 1</a>
              <a class="link" href="">example 2</a>
              <a class="link" href="">example 3</a>
              <a class="link" href="">example 4</a>
              <a class="link" href="">example 5</a>
            </div>
            <div class="link" (click)="this.selectItem = 2">
                <i class="fas fa-file link-icon"></i>
                  UI components 2
                <i class="fas toggle" [ngClass]="{'fa-angle-down' : this.selectItem === 2, 'fa-angle-right':this.selectItem !== 2}"></i>
              </div>
              <div *ngIf="this.selectItem === 2">
                <a class="link" href="">example 6</a>
                <a class="link" href="">example 7</a>
                <a class="link" href="">example 8</a>
                <a class="link" href="">example 9</a>
                <a class="link" href="">example 10</a>
              </div>

              <div class="link" (click)="this.selectItem = 3">
                <i class="fas fa-file link-icon"></i>
                  UI components 3
                <i class="fas toggle" [ngClass]="{'fa-angle-down' : this.selectItem === 3, 'fa-angle-right':this.selectItem !== 3}"></i>
              </div>
              <div *ngIf="this.selectItem === 3">
                <a class="link" href="">example 11</a>
                <a class="link" href="">example 12</a>
                <a class="link" href="">example 13</a>
                <a class="link" href="">example 14</a>
                <a class="link" href="">example 15</a>
              </div>


          </div>
        </div>
      </div>
      <div class="container-body" style="padding:30px">
        <span >
          <p>Click this menu button to see how sidebar works.</p>
          <BHPIcon class="fas fa-bars" size="48" (click)="isOpen=!isOpen"></BHPIcon>
        </span>
      </div>
    </div>


`
})
export class Example {
  selectItem: number = 1;
}



