import { Component } from "@angular/core";
@Component({
  selector: "example",
  template: `
    <div class="input-group">
      <input [(ngModel)]="content" type="text" class="form-control" placeholder="Search" aria-label="Search" aria-describedby="basic-addon2">

      <div class="input-group-append">
        <button class="btn btn-outline-secondary" type="button" (click)="content=''">
          <i class="fas fa-times md-18 align-bottom"></i>
        </button>
        <button class="btn btn-outline-secondary" type="button">
          <i class="fas fa-search md-18 align-bottom"></i>
        </button>
      </div>
    </div>
    <div>
      Input is: {{content}}
    </div>`
})
export class Example {}



