import { Component } from "@angular/core";
@Component({
  selector: "example",
  template: `
    <div><span class="text-muted">The quick brown fox jumps over the lazy dog.</span></div>
    <div><span class="text-primary">The quick brown fox jumps over the lazy dog.</span></div>
    <div><span class="text-warning">The quick brown fox jumps over the lazy dog.</span></div>
    <div><span class="text-danger">The quick brown fox jumps over the lazy dog.</span></div>
    <div><span class="text-success">The quick brown fox jumps over the lazy dog.</span></div>
    <div><span class="text-info">The quick brown fox jumps over the lazy dog.</span></div>`
})
export class Example {}



