import { Component } from "@angular/core";
@Component({
  selector: "example",
  template: `
    <div style="height: 250px">
      <Button class="btn" btnColor="primary outline" (click)="modal.openBHPModal()">Launch Modal</Button>
      <BHPModal #modal [context]="{name: 'All changes you just made have been saved to the database.'}">
        <div class="modal-header">
          <h4 class="modal-title pull-left">Changes saved</h4>
          <button type="button" class="close pull-right" aria-label="Close" (click)="modal.closeBHPModal()">
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          {{modal.context.name}}
        </div>
        <div class="modal-footer">
          <Button class="btn" btnColor="secondary" (click)="modal.closeBHPModal()">Close</Button>
        </div>
      </BHPModal>
    </div>`
})
export class Example {}



