import { Component } from "@angular/core";
@Component({
  selector: "example",
  styleUrls: ["./appbar0.scss"],
  template: `
  <BHPAppBar class="bg-bhp-orange-1 appbar-container">
    <i icon class="bg-bhp-orange-1  fas fa-bars md-light icon"></i>
    <BHPLogo logo color="white" ></BHPLogo>
    <div class="appbar-right dropdown">
        <Button dropdownToggle btnColor="primary" type="button" class="btn dropdown-toggle">
          <span class="caret"></span>
        </Button>
        <ul *dropdownMenu class="dropdown-menu" role="menu">
          <li role="menuitem">
            <a class="dropdown-item" href="#">Action</a>
          </li>
          <li role="menuitem">
            <a class="dropdown-item" href="#">Another action</a>
          </li>
          <li role="menuitem">
            <a class="dropdown-item" href="#">Something else here</a>
          </li>
          <li class="divider dropdown-divider"></li>
          <li role="menuitem">
            <a class="dropdown-item" href="#">Separated link</a>
          </li>
        </ul>
    </div>
  </BHPAppBar>`
})
export class Example {}



