import { Component } from "@angular/core";
@Component({
  selector: "example",
  template: `
    <div style="padding: 50px">
      <Button type="button" class="btn btn-default btn-secondary mb-2"
              tooltip="Vivamus sagittis lacus vel augue laoreet rutrum faucibus."
              placement="top">
        Tooltip on top
      </Button>

      <Button type="button" class="btn btn-default btn-secondary mb-2"
              tooltip="Vivamus sagittis lacus vel augue laoreet rutrum faucibus."
              placement="right">
        Tooltip on right
      </Button>

      <Button type="button" class="btn btn-default btn-secondary mb-2"
              tooltip="Vivamus sagittis lacus vel augue laoreet rutrum faucibus."
              placement="auto">
        Tooltip auto
      </Button>

      <Button type="button" class="btn btn-default btn-secondary mb-2"
              tooltip="Vivamus sagittis lacus vel augue laoreet rutrum faucibus."
              placement="left">
        Tooltip on left
      </Button>

      <Button type="button" class="btn btn-default btn-secondary mb-2"
              tooltip="Vivamus sagittis lacus vel augue laoreet rutrum faucibus."
              placement="bottom">
        Tooltip on bottom
      </Button>
    </div>`
})
export class Example {}



