import {
  Component,
  Input,
  OnInit,
  ViewChild,
  OnDestroy,
  ComponentFactoryResolver,
  Type
} from "@angular/core";
import { ActivatedRoute, Router, NavigationEnd } from "@angular/router";
import * as yamlJS from "js-yaml";
import { ExampleHost } from "./example.host.directive";
import { ExamplePartComponent } from "./example.part.component";
import Remarkable from "remarkable";

const md = new Remarkable({
  html: true, // Enable HTML tags in source
  xhtmlOut: false, // Use '/' to close single tags (<br />)
  breaks: false, // Convert '\n' in paragraphs into <br>
  linkify: false, // Autoconvert URL-like text to links

  // Enable some language-neutral replacement + quotes beautification
  typographer: false
});

@Component({
  selector: "Example",
  template: `
            <section>
              <h3 *ngIf="title && title.length>0"
                class="page-header">
                  {{title}}
              </h3>
              <div [innerHTML]="preText">
              </div>
              <ng-template example-host></ng-template>
            </section>
            `,
  styleUrls: ["./example.component.scss"]
})
export class ExampleComponent implements OnInit, OnDestroy {
  @ViewChild(ExampleHost) adHost: ExampleHost;

  @Input()
  set example(value: any) {
    if (!value) {
      console.error("Set example does not accecpt null");
    } else {
      if (Array.isArray(value)) {
        this._example = value;
      } else {
        this._example = [value];
      }
      this.loadComponent();
    }
  }
  get example() {
    return this._example;
  }

  _example: Type<any>[];
  sub: any;
  html: string;
  title: string;
  preText: string;
  yaml: any;

  constructor(
    private componentFactoryResolver: ComponentFactoryResolver,
    private activatedRoute: ActivatedRoute,
    private router: Router
  ) {}

  ngOnInit() {
    //get router data and init dynamic components
    this.activatedRoute.firstChild.data
      .subscribe(data => {
        this.yaml = data.yaml;
        this.example = data.components;
      })
      .unsubscribe();

    // when router changed, this subscribe function will be triggered
    this.sub = this.router.events
      .filter(event => event instanceof NavigationEnd)
      .map(() => this.activatedRoute)
      .map(route => route.firstChild)
      .switchMap(route => route.data)
      .subscribe(data => {
        const { components, yaml } = data;
        if (yaml) {
          this.yaml = yaml;
        }
        if (components) {
          this.example = components;
        } else {
          console.error(
            "Cannot find components from router data.",
            this.router
          );
        }
      });
  }

  ngOnDestroy() {
    if (this.sub) {
      this.sub.unsubscribe();
    }
  }

  loadComponent() {
    if (this.yaml.title) {
      this.title = this.yaml.title || "";
    }
    if (this.yaml.preText) {
      this.preText = md.render(this.yaml.preText);
    } else {
      this.preText = "";
    }

    // create dynamic components based on yaml files.
    let viewContainerRef = this.adHost.viewContainerRef;
    viewContainerRef.clear();

    this._example.forEach((example, index) => {
      var cmpRef = viewContainerRef.createComponent(
        this.componentFactoryResolver.resolveComponentFactory(
          ExamplePartComponent
        )
      );
      const { subTitle, title, introduction } = this.yaml.examples[index];
      cmpRef.instance.subTitle = subTitle;
      cmpRef.instance.title = title;
      cmpRef.instance.introduction = introduction;
      cmpRef.instance.example = example;
    });
  }
}



